
@media (max-width: 869px) {
  .who-we-are-section-container {
    display: flex;
    flex-direction: column;
    flex: 1;
    color: white;
    background-color: #5052b5;
  }

  .sub-section-container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  .sub-section-container:nth-child(2) {
    background-color: rgba(0, 0, 0, 0.05);
  }

  .sub-section-container:nth-child(3) {
    background-color: rgba(0, 0, 0, 0.1);
  }

  .sub-section-text-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 4em 2em 2em 4em;
    text-align: left;
    flex-grow: 1;
  }

  .sub-section-text-content h2 {
    padding: 0;
  }

  .sub-section-text-content p {
    color: rgba(255, 255, 255, 0.55);
    font-size: 17px;
    padding-bottom: 2em;
  }
}

